import parseHtml from "@/common/html-parser.js"
export default {
	onShareAppMessage(res) {
	    if (res.from === 'button') {
	       console.log('点击了页面的转发按钮')
	       console.log(res.target)
	    }else{
			console.log('点击了原生转发')
		}
		
	    return {
	       title: this.content.title,
	       path: "/pages/service/detail?id=" + this.biId,
		}
	},
	data() {
		return {
			biId:'',
			authKey:'',
			content:'',
			contentTxt:'',
			contentArr: [],
			video_src: '',
			path:'pages/knowledge/detail'
		};
	},
	onLoad(e) {			
		this.authKey = uni.getStorageSync('authKey');
		this.biId = e.id;
		// 处理判断sence
		if(e.scene){
			this.biId = e.scene
		}
		this.getListDetail();
		
		
	},
	computed:{
		backImage(){
			return this.static + this.content.cover_src
		}
	},
	methods:{
		getListDetail(){
			var that = this;
			uni.request({
				url: this.http+'knowledge/detail/' + this.biId,
				method: 'GET',
				header: {
					authKey: this.authKey
				},
				data: {},
				success: res => {
					if (res.data.code == 200) {
						this.content = res.data.data
						this.video_src = this.static + res.data.data.video_src
						let content = parseHtml(res.data.data.content)
						content.forEach(function(item){
							that.adjustimg(item)
						})
						this.contentTxt = content
					}
				},
			});
		},
		adjustimg(node){
			let that = this
			if (node.name && node.name == 'img'){
				node.attrs['style'] = 'margin: 6px 0;width:100%;'
			} else if (node.name && node.children && node.children.length > 0){
				node.children.forEach(function(item){
					that.adjustimg(item)
				})
			}
		},
		// 收藏
		collect(id){
			var that = this;
			uni.request({
				url: this.http+'collection/append/' + id,
				method: 'GET',
				header: {
					authKey: this.authKey
				},
				data: {
					
				},
				success: res => {
					if (res.data.code == 200) {
						uni.showToast({
							title: '收藏成功',
							icon:'none',
							mask: false,
							duration: 1500
						});
						that.getListDetail();
					}
				},
			});
		},
		// 取消收藏
		cancel(id){
			var that = this;
			uni.request({
				url: this.http+'collection/remove/' + id,
				method: 'GET',
				header: {
					authKey: this.authKey
				},
				data: {
					
				},
				success: res => {
					if (res.data.code == 200) {
						uni.showToast({
							title: '取消收藏成功',
							icon:'none',
							mask: false,
							duration: 1500
						});
						let obj = that.getListDetail()
						console.log(obj)
					}
				},
			});
		}		
	}
}
